___CSS_LOADER_EXPORT___.push([module.id, `#about-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
#about-container > iframe {
  margin-bottom: 15px;
}

#para-container {
  display: flex;
  flex-direction: column;
  gap: 22px;
  padding: 7px 14px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.525);
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(15px);
}

#para-container > p {
  font-size: 1rem;
  font-weight: 500;
}
  
#about-container iframe {
    width: 600px;
    height: 400px;
}

@media only screen and (max-width: 625px) {
  #about-container iframe {
    width: 400px;
    height: 275px;
  }
}

@media only screen and (max-width: 420px) {
  #about-container iframe {
    width: 300px;
    height: 175px;
  }
}`, "",{"version":3,"sources":["webpack://./src/components/about/about.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,iBAAiB;EACjB,mBAAmB;EACnB,4CAA4C;EAC5C,4CAA4C;EAC5C,2BAA2B;AAC7B;;AAEA;EACE,eAAe;EACf,gBAAgB;AAClB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;EACE;IACE,YAAY;IACZ,aAAa;EACf;AACF;;AAEA;EACE;IACE,YAAY;IACZ,aAAa;EACf;AACF","sourcesContent":["#about-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n  \n#about-container > iframe {\n  margin-bottom: 15px;\n}\n\n#para-container {\n  display: flex;\n  flex-direction: column;\n  gap: 22px;\n  padding: 7px 14px;\n  border-radius: 10px;\n  background-color: rgba(255, 255, 255, 0.525);\n  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);\n  backdrop-filter: blur(15px);\n}\n\n#para-container > p {\n  font-size: 1rem;\n  font-weight: 500;\n}\n  \n#about-container iframe {\n    width: 600px;\n    height: 400px;\n}\n\n@media only screen and (max-width: 625px) {\n  #about-container iframe {\n    width: 400px;\n    height: 275px;\n  }\n}\n\n@media only screen and (max-width: 420px) {\n  #about-container iframe {\n    width: 300px;\n    height: 175px;\n  }\n}"],"sourceRoot":""}]);
